.blogs {
    position: relative;
    padding: 120px 0 50px;
    background: url('../images/bg7.jpg') no-repeat center center;
    background-size: cover;

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $bg-color-dark;
        z-index: 0;

    }

    .blog-content {
        margin-top: 70px;

        .blog-items {
            position: relative;
            margin-bottom: 50px;
            overflow: hidden;
            &:hover{
                background-color: $third-color;
                .images-box{
                    img{
                        transform: scale(1.2);
                    }
                }
            }
            .images-box {
                position: relative;
                overflow: hidden;
                height: 300px;
                border-bottom: 1px solid #b46e3c;
                img{
                    transform: scale(1);
                    transition: all 0.5s ease;
                    }
            }

            .date {
                position: absolute;
                width: 80px;
                height: 80px;
                left: 25px;
                background-color: $third-color;
               top: 255px;
                text-align: center;

                .day {
                    font-size: 36px;
                    line-height: 36px;
                    color: #ffffff;
                    margin: 10px 0 5px;
                    font-weight: 700;
                }

                .month {
                    text-transform: uppercase;
                    color: rgba(255, 255, 255, 0.7);
                    font-size: 14px;
                    line-height: 16px;
                    letter-spacing: 2px;
                }
            }

            .blog-info {
                .links {
                    font-size: 12px;
                    color: rgba(255, 255, 255, 0.5);
                    line-height: 40px;
                   text-indent: 130px;
                   a{
                       text-decoration: none;
                       color: rgba(255, 255, 255, 0.5);
                   }
                }

                .blog-title {
                    font-size: 15px;
                    text-decoration: none;
                    color: #ffffff;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    text-decoration: none;
                    padding: 5px 25px 20px;
                    display: block;
                    font-weight: 600;
                }
            }

        }
    }
}