.footer{
    position: relative;
    padding-top: 100px;
    background-color: $bg-color;
    .footer-title{
        position: relative;
        color: #ffff;
        font-weight: 600;
        margin-bottom: 50px;
        padding-bottom: 35px;
        font-size: 15px;
        text-transform: uppercase;
        &:before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            background-color: $third-color;
            bottom: 1px;
            left: 0;
            transform: rotate(45deg);
        }

        &:after {
            position: absolute;
            content: "";
            width: 150px;
            border-bottom: 1px solid $third-color;
            bottom: 5px;
            left: 0;
        }
    }
    .list-flicker{
        list-style: none;
        display: inline-block;
        margin-bottom: 50px;
        li{
            float: left;
            margin: 5px;
            background-color: #ffffff;
            width: 150px;
            opacity: 0.7;
            cursor: pointer;
            &:hover{
                opacity: 1;
            }
        }
    }
    a{
        text-decoration: none;
        font-size: 15px;
        color:  rgba(255, 255, 255, 0.7);
        &:hover{
            color: #ffffff;
        }
    }
    ul{
        li{
            margin-bottom: 5px;
        }
    }
    .footer-bottom{
        padding: 15px 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        margin-top: 50px;
        .social-icon{
            margin: 0;
            li{
                display: inline-block;
                margin-right:  10px;
                margin-top: 10px;
                a{
                   
                    color: rgba(255, 255, 255, 0.5);
                    text-align: right;
                    line-height: 40px;
                    font-size: 26px;
                    margin: auto;
                    display: block;
                    width: 30px;
                    transition: all 0.3s ease;
                    &:hover{
                        color: #ffffff;
                    }
                }
            }
        }
    }
   
}