.about {
    position: relative;
    background: url('../images/bg4.jpg') no-repeat center center;
    background-size: cover;
    padding: 120px 0 100px;

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $bg-color-dark;
        z-index: 0;

    }

   
    .about-content-box {
        margin-top: 70px;
        z-index: 1;

        .about-carousel {
            text-align: center;

            .owl-dots {
                .owl-dot {
                    height: 20px;
                    width: 10px;
                    border: 1px solid rgba(255, 255, 255, 0.3);
                    margin: 0 5px;
                    display: inline-block;
                }

                .active {
                    background-color: $third-color;
                    border-color: $third-color;
                }
            }
        }

        .about-content {
            .head-title {
                color: #fff;
                padding-bottom: 35px;
                margin-bottom: 50px;
                position: relative;

                &::before {
                    position: absolute;
                    content: "";
                    width: 10px;
                    height: 10px;
                    background: $third-color;
                    transform: rotate(45deg);
                    bottom: 0.5px;
                }

                &:after {
                    position: absolute;
                    content: "";
                    width: 150px;
                    height: 1px;
                    bottom: 5px;
                    left: 0;
                    background: $third-color;
                }
            }

            .icon-box {
               margin-top: 50px;
                .icon-item {
                    display: flex;
                    margin-bottom: 10px;
                    .icon {
                        line-height: 26px;
                        font-size: 40px;
                        color: #b46e3c;
                    }

                    .content-text {
                        margin-left: 20px;
                        a {
                            text-decoration: none;
                            color: #ffffff;
                            margin: 3px 0 10px;
                            display: block;
                            font-size: 15px;
                            letter-spacing: 1px;
                            font-weight: 600;
                            text-transform: uppercase;
                        }
                    }
                }
            }
        }
    }

}