.featured {
    position: relative;
    padding: 100px 0;
    background: $bg-color;

    .featured-title {
        color: #ffffff;
        position: relative;
        padding: 0 0 35px 0;
        margin-bottom: 50px;
        font-weight: 400;
        line-height: 1.1;
        text-align: center;

        &:before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            background-color: $third-color;
            transform: rotate(45deg);
            bottom: 1px;
            left: 50%;
        }

        &:after {
            position: absolute;
            content: "";
            height: 1px;
            width: 150px;
            background-color: $third-color;
            bottom: 5px;
            left: 15%;
        }
    }

    .featured-content {
        margin-top: 70px;

        .featured-items {
            .head-title {
                position: relative;
                text-transform: uppercase;
                line-height: 36px;
                font-size: 15px;
                font-weight: 600;
                letter-spacing: 1px;
                padding: 0 0 30px 0;
                margin: 0 0 30px;
                color: #ffffff;

                &:before {
                    position: absolute;
                    content: "";
                    width: 10px;
                    height: 10px;
                    background-color: $third-color;
                    transform: rotate(45deg);
                    bottom: 1px;
                   
                }

                &:after {
                    position: absolute;
                    content: "";
                    height: 1px;
                    width: 150px;
                    background-color: $third-color;
                    bottom: 5px;
                  
                }
            }

        }
        .right{
            text-align: right;
            .head-title {
                &:before{
                    right: 0;
                }
                &:after{
                    right: 0;
                }
            }
        }
        .left{
            text-align: left;
            .head-title {
                &:before{
                   left: 0;
                }
                &:after{
                    left: 0;
                }
            }
        }
    }
}