.header {
    position: relative;
    background-color: $bg-color;
    overflow: hidden;
    .navbar {
        padding: 0;

        .navbar-brand {
            margin-left: 40px;
            margin-top: -3px;
        }

        #my-nav {
            position: relative;

            .navbar-nav {
              
                .nav-item {
                    padding: 35px 30px;
                    transition: all 0.5s ease;
                    cursor: pointer;
                    
                   
                    .nav-link {
                        text-transform: uppercase;
                        color: #ffffff;
                        font-family: 'Opensans', sans-serif;
                        font-size: 13px;
                        letter-spacing: 1px;
                        display: block;
                        font-weight: 500;
                        
                            
                    }
                    &:hover{
                        padding: 35px 250px 35px 35px;
                        .nav-link{
                            color: $third-color;
                        }
                        
                    }
                }
                .active{
                    .nav-link{
                        color: $third-color;
                        font-weight: 700;
                    }
                }
                .home-bg {
                    background: url("../images/bg2.jpg") no-repeat center center /cover;
                }

                .about-bg {
                    background: url("../images/bg4.jpg") no-repeat center center /cover;
                }

                .services-bg {
                    background: url("../images/bg3.jpg") no-repeat center center /cover;
                }

                .works-bg {
                    background: url("../images/bg5.jpg") no-repeat center center /cover;
                }

                .blogs-bg {
                    background: url("../images/bg7.jpg") no-repeat center center /cover;
                }

                .contact-bg {
                    background: url("../images/bg6.jpg") no-repeat center center /cover;

                }
            }
        }
    }
    .sticky-anema{
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 99999;
        background: #343434;
        transition: all 0.3s ease;
        box-shadow: 0px 5px 40px -15px rgba(202, 173, 173, 0.75);
        li{
            padding: 30px !important;
            &:hover{
                padding: 30px 250px 30px 30px !important;
                transition: all 0.3s ease;
            }
        }
    }
}

.ml-auto {
    margin-left: auto !important;
}