.block-one {
    position: relative;
    background-color: $bg-color;
    padding: 100px 0 50px;

    .block-title {
        text-align: center;

        .bg-title {
            position: relative;
            color: $primary-color;
            font-size: 36px;
            padding-bottom: 35px;
            margin-bottom: 50px;
            font-weight: 400;
            display: block;
            line-height: normal;

            &:before {
                position: absolute;
                content: "";
                width: 10px;
                height: 10px;
                background-color: $third-color;
                bottom: 0;
                left: 50%;
                transform: rotate(45deg);
            }

            &:after {
                position: absolute;
                content: "";
                width: 150px;
                border-bottom: 1px solid $third-color;
                bottom: 5px;
                left: 40%;
            }

            span {
                color: $third-color;
            }
        }

        .block-text {
            color: $second-color;

        }
    }
}

.block-two {
    position: relative;
    background: #222222;
    padding: 100px 0;
    overflow: hidden;

    .welcome-article {
        background: #171717;
        overflow: hidden;

        &:hover {
            img {
                transform: scale(1.1);
                transition: all 0.5s ease;
            }
        }

        img {
            display: block;

        }

        .welcome-article-text {
            text-align: center;
            padding: 30px;

            h3 {
                color: #ffffff;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-weight: 600;
                font-size: 15px;
                line-height: 15px;
                padding-bottom: 25px;
                margin: 0 0 25px 0;
                border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }

            p {
                color: rgba(255, 255, 255, 0.5);
                padding: 0 30px 25px;
                margin: 0 0 25px 0;
                border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }
        }
    }

    .bg-dark-c {
        background: #202020 !important;
    }
}

.block-three {
    position: relative;
    padding: 100px 0;
    background: $bg-color;
    text-align: center;

    h2 {
        position: relative;
        color: #ffffff;
        padding-bottom: 35px;
        margin-bottom: 50px;
        font-weight: 400;

        &:before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            transform: rotate(45deg);
            bottom: 1px;
            background: $third-color;
            left: 50%;
        }

        &:after {
            position: absolute;
            content: "";
            width: 150px;
            height: 1px;
            background: $third-color;
            bottom: 5px;
            left: 40%;
        }
    }

    .form-group {
        padding-bottom: 10px;

        .form-control {
            box-sizing: border-box;
            border: none;
            border-left: 1px solid rgba(255, 255, 255, 0.3);
            border-right: 1px solid rgba(255, 255, 255, 0.3);
            color: #b46e3c;
            line-height: inherit;
            padding: 6px 20px;
            width: 100%;
            font-size: 14px;
            background: #191919;
            margin-bottom: 25px;
            text-align: center;
            outline: none;
            border-radius: 0;
        }

    }

}


.block-four {
    position: relative;
    padding: 100px 0;
    background-color: $bg-color;

    .block-title {
        position: relative;
        color: #ffffff;
        padding-bottom: 35px;
        margin-bottom: 50px;

        &:before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            background-color: $third-color;
            bottom: 1px;
            left: 48%;
            transform: rotate(45deg);
        }

        &:after {
            position: absolute;
            content: "";
            width: 150px;
            border-bottom: 1px solid $third-color;
            bottom: 5px;
            left: 20%;
        }

    }

    .partners-owlcarousel {
        padding: 30px 0;

        .partners-items {
            margin: 50px 30px;
            opacity: 0.6;
            border: 5px double $third-color;
        }

        .center {
            transform: scale(1.5);
            margin-right: 10px;
        }
    }
}