.home {
    position: relative;
    overflow: hidden;
    height: 500px;
    width: 100%;
    overflow: hidden;
    
    .swiper-container {
        width: 100%;
        height: 100%;

        .swiper-slide {
            width: 100%;
            position: relative;
            text-align: center;
            font-size: 18px;
            background: $bg-color;
            display: flex;
            justify-content: center;
            align-items: center;
            .images-box{
                img{
                    display: block;
                    width: 100%;
                }
            }
            .content-box{
                width: 30%;
                padding: 20px;
                h4{
                    color: $third-color;
                    font-size: 25px;
                    text-transform: capitalize;
                    margin-bottom: 20px;
                }
                h2{
                    color: #ffffff;
                    font-weight: 700;
                    font-size: 40px;
                    text-transform: uppercase;
                }
            }  
        }
        .swiper-pagination-bullet {
            width: 30px;
            height: 30px;
            text-align: center;
            line-height: 30px;
            font-size: 12px;
            color: #000;
            opacity: 1;
            font-weight: 500;
            background: rgba(0, 0, 0, 0.2);
        }
    
        .swiper-pagination-bullet-active {
            color: #fff;
            background: $third-color;
        }
    }

   
}