.contact {
    position: relative;
    padding: 120px 0 100px;
    background: url('../images/bg6.jpg') no-repeat center center /cover;

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $bg-color-dark;
        z-index: 0;

    }

    .contact-title {
        color: #ffffff;
        padding-bottom: 35px;
        margin-bottom: 50px;
        position: relative;

        &:before {
            position: absolute;
            content: "";
            width: 10px;
            height: 10px;
            background-color: $third-color;
            bottom: 1px;
            left: 48%;
            transform: rotate(45deg);
        }

        &:after {
            position: absolute;
            content: "";
            width: 150px;
            border-bottom: 1px solid $third-color;
            bottom: 5px;
            left: 35%;
        }
    }

    .adress-information {
        .phone {
            margin: 0 0 20px 0;
            color: #ffffff;
            font-size: 15px;
            margin-top: 20px;
            letter-spacing: 1px;
            font-weight: 600;
            text-transform: uppercase;

            i {
                color: $third-color;
                margin-right: 10px;
            }
        }

        .email {
            margin: 0 0 20px 0;
            color: #ffffff;
            font-size: 15px;
            letter-spacing: 1px;
            font-weight: 600;
            text-transform: uppercase;
            margin-top: 20px;

            i {
                color: $third-color;
                margin-right: 10px;
            }
        }

        p {
            margin-bottom: 5px;
        }

    }

    .get-in-touch {
        .form-group {
            margin-bottom: 50px;

            .form-control {
                border: none ;
                outline: none;
                padding: 6px 20px;
                border-radius: 0;
                margin-bottom: 30px;
                text-align: center;
                background-color: $bg-color;
                border-left: 1px solid $third-color;
                border-right: 1px solid $third-color;
                color: $third-color;
            }
        }
    }
}
.google-map{
    height: 400px;
    iframe{
        overflow: hidden;
        height: 100% !important;
        width: 100% !important;
        border: none;
        pointer-events: none;
    }
}