.portfolio {
    position: relative;
    padding: 120px 0 100px;
    background: url('../images/bg5.jpg') no-repeat center center;
    background-size: cover;
    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $bg-color-dark;
        z-index: 0;

    }
    .portfolio-carousel{
        .portfolio-items {
            position: relative;
            text-align: center;
            margin-top: 70px;
            margin-right: 10px;
            overflow: hidden;
            &:hover {
                .images-box {
                    .overlay-img {
                        opacity: 0.8;
                    }
                    img{
                        transform: scale(1.2);
                    }
                    &:before {
                        height: 100%;
                    }
    
                    &:after {
                        height: 100%;
                    }
                    .portfolio-content{
                        opacity: 1;
                    }
                }
            }
    
            .images-box {
                position: relative;
                cursor: pointer;
    
                .overlay-img {
                    position: absolute;
                    content: "";
                    top: 0;
                    right: 0;
                    left: 0;
                    bottom: 0;
                    background-color: rgba(34, 34, 34, 0.9);
                    z-index: 1;
                    opacity: 0;
                    transition: all 0.5s ease;
                }
                img{
                    transform: scale(1);
                    transition: all 0.5s ease;
                }
                &:before {
                    position: absolute;
                    content: "";
                    width: 10px;
                    background: $third-color;
                    height: 0;
                    top: 0;
                    right: 0;
                    z-index: 2;
                    transition: all 0.5s ease;
                }
    
                &:after {
                    position: absolute;
                    content: "";
                    width: 10px;
                    height: 0;
                    bottom: 0;
                    left: 0;
                    background: $third-color;
                    z-index: 2;
                    transition: all 0.5s ease;
                }
    
                .portfolio-content {
                    position: absolute;
                    top: 40%;
                    left: 35%;
                    z-index: 2;
                    opacity: 0;
                    transition: all 0.3s ease;
                    h4 {
                        text-transform: uppercase;
                        color: #ffffff;
                        letter-spacing: 1px;
                        font-weight: 500;
                        font-size: 15px;
                    }
    
                    p {
                        color: rgba(255, 255, 255, 0.7);
                    }
                }
            }
        }
        .owl-dots {
            text-align: center;
            .owl-dot {
                height: 20px;
                width: 10px;
                border: 1px solid rgba(255, 255, 255, 0.3);
                margin: 0 5px;
                display: inline-block;
            }

            .active {
                background-color: $third-color;
                border-color: $third-color;
            }
        }
    }
}