
body{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 14px;
}
a{
    text-decoration: none !important;
}
ol,ul{
    list-style: none;
    padding-left: 0;
}

p {
    color: $second-color;
    font-family: 'Opensans', sans-serif;
    font-size: 14px;
    line-height: 26px;
}
.no-padding{
    padding: 0;
}

.section-title {
    z-index: 1;
    margin-bottom: 50px;
    text-align: center;
    .head-title {
        position: relative;
        color: #ffffff;
        padding: 0 0 25px;
        margin: 0;
        font-size: 60px;
        line-height: 60px;
        text-transform: uppercase;
        text-align: center;
        letter-spacing: 3px;

    }

    .sec-col {
        width: 2px;
        height: 50px;
        background-color: $third-color;
        margin: 0 auto 25px;
    }

    .head-text {
        color: $second-color;
        text-transform: uppercase;
    }
}
.btn-default {
    transition: all 0.3s ease;
    text-decoration: none;
    font-size: 14px;
    text-transform: uppercase;
    padding: 11px 15px;
    font-family: 'Opensans', sans-serif;
    border: 1px solid rgba(34, 34, 34, 0.3);
    color: rgba(255, 255, 255, 0.5);
    background: none;
    border-color: rgba(255, 255, 255, 0.3);

    &:hover {
        background: $third-color;
        color: #ffffff;
    }

}
.scroll-top{
    position: fixed;
    background: url('../images/ui.totop.png') no-repeat left top;
    background-size: cover;
    width: 48px;
    height: 48px;
    right: 20px;
    bottom: 20px;
    transform: rotate(45deg);
    border: 7px double $third-color;
    cursor: pointer;
}