.services{
    position: relative;
    background: url('../images/bg5.jpg') no-repeat center center;
    background-size: cover;
    padding: 120px 0 50px;

    &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: $bg-color-dark;
        z-index: 0;

    }
   
    .services-items{
        position: relative;
        display: flex;
        margin-bottom: 30px;
        padding: 20px;
        transition: all 0.5s ease;
        &:before{
            position: absolute;
            content: "";
            border: 2px solid $third-color;
            transform: scale(0);
            transform-origin: (50%,50%);
            transition: all 0.5s ease;
        }
        &:hover{
            
            &:before{
                position: absolute;
                content: "";
                transform: scale(1);
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
            }
            transform: scale(0.9);
        }
       
        .icon-box{
            i{
                color: #fff;
                font-size: 50px;
            }
        }
        .services-content{
            margin-left: 20px;
            h3{
                color: #ffffff;
                margin: 0 0 25px;
                font-size: 15px;
                letter-spacing: 1px;
                font-weight: 600;
                font-family: 'JosefinSans', sans-serif;
                text-transform: uppercase;
                transition: all .3s;
            }
        }
    }
}